import React, { useState, useEffect } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Button from '@material-ui/core/Button';
import QrReader from 'react-qr-reader'
import Dialog from '@material-ui/core/Dialog';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import styled from 'styled-components'
import { ListItemIcon } from "@material-ui/core";
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestoreIcon from '@material-ui/icons/Restore';
import Popover from '@material-ui/core/Popover';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';

const QR_STORAGE_KEY = 'QR_STORAGE_KEY'

const Wrapper = styled.div`
  max-width: 420px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`

const Bottom = styled.div`
  background-color: green;
  width: 100%;
  position: fixed;
  bottom: 20px;
  left: 0;
`

const getFromStorage = key => typeof window !== 'undefined' && JSON.parse(window.localStorage.getItem(key)) || [];

const IndexPage = () => {
  const [result, setResult] = useState('No result')
  const [disabled, setDisabled] = useState(true)
  const [list, setList] = useState(getFromStorage(QR_STORAGE_KEY));

  const updateItem = (key, value) => {
    typeof window !== 'undefined' && window.localStorage.setItem(QR_STORAGE_KEY, JSON.stringify(value));
    setList(getFromStorage(QR_STORAGE_KEY))
  }
  

  return <Layout>
    <SEO title="Home" />

    {
            !disabled && <QrReader
            delay={300}
            onError={err => console.error(err)}
            onScan={data => {
                if(data) {
                  data && setResult(data)
                  setDisabled(true)
                  setList([
                    ...list,
                    { 
                      data,
                      timestamp: (new Date()).getTime()
                    }
                  ])
                }
              }
            }
            style={{ width: '100%' }}
            />
    }
    {
      !!disabled && <List component="nav" aria-label="main mailbox folders">
        {
          list.map(e => 
            <>
          <ListItem button>
              <ListItemText primary="Drafts" />
          </ListItem>
          <Divider />
          </>
          )
        }
      </List>
    }

    
    <Bottom>
    <BottomNavigation

    >
    <Button onClick={() => setDisabled(!disabled)}  variant="outlined"  color="primary"> { disabled ? 'Scan' : 'Home' }</Button>    
    </BottomNavigation>
    </Bottom>
  </Layout>
}

export default IndexPage
